window.addEventListener('load', showPreloader);

const LS_RULES_KEY = "uaRules";

document.addEventListener('DOMContentLoaded', () => {
    const preloader = document.querySelector('.preloader');
    preloader.querySelector(".join-block").addEventListener('click', hidePreloader)

    const popUp = document.querySelector(".first-page-popup")
    const popUpNo = popUp.querySelector(".buttons-no")
    const popUpYes = popUp.querySelector(".buttons-yes")
    const popUpCheckBox = popUp.querySelector("input")

    if (localStorage.getItem(LS_RULES_KEY)) {
        popUpCheckBox.checked = true;
        popUpCheckBox.style.borderColor = undefined
    }
    const hidePopUp = () => {
        popUp.classList.remove('visible')
        popUp.classList.add('hidden')
    }
    const visiblePopUp = () => {
        popUpCheckBox.checked = !!localStorage.getItem(LS_RULES_KEY);
        popUp.classList.remove('hidden')
        popUp.classList.add('visible')
    }

    popUpNo.addEventListener('click', hidePopUp)
    popUpYes.addEventListener('click', () => {
        localStorage.setItem(LS_RULES_KEY, popUpCheckBox.checked)
        if (popUpCheckBox.checked) {
            popUpCheckBox.style.borderColor = "#f9b235";
            hidePopUp()
        } else {
            localStorage.removeItem(LS_RULES_KEY)
            popUpCheckBox.style.borderColor = 'red'
        }
    })
    document.querySelectorAll(".open-popup").forEach((item) => {
        item.addEventListener('click', visiblePopUp)
    })
    navigationInit();
    // first page

    //                                                   для прода розкомітит
    // try {
    //     player();
    // } catch (error) {
    //     console.warn(error, 'errror audio player')
    // }

    const firstPage = document.querySelector('.first-page')
    const joinCircle = firstPage.querySelector(".join-circle")
    const joinGit = firstPage.querySelector(".join-anima")
    const joinText = firstPage.querySelector(".join-block-text")

    joinCircle.addEventListener("mouseover", (e) => {
        joinCircle.classList.add('active')
        joinGit.classList.add('active')
    })


    joinText.addEventListener("mouseover", (e) => {
        joinCircle.classList.add('active')
        joinGit.classList.add('active')
    })

    joinCircle.addEventListener("mouseout", () => {
        joinCircle.classList.remove('active')
        joinGit.classList.remove('active')
    })

    joinCircle.addEventListener('click', ()=> {
        console.log("joinCircle clock")
    })
    window.addEventListener('scroll', () => {
        navigationInit() // запускаем функцию при скролле страницы
        headerScroll();
        visibleHeaderLogo();
    })
    window.addEventListener('resize', () => {
        navigationInit() // запускаем функцию при ресайзе страницы
    })


    // two page

    const counter = document.querySelector(".counter")
    const counterCircle = document.querySelector(".counter-circle")
    const counterLineLeft = document.querySelector(".counter-line-left")
    const counterLineRight = document.querySelector(".counter-line-right")

    if( window.innerWidth <= 992) {

        function switchActiveLeft() {
            counter.classList.add('active-left')
            counter.classList.add('active-left')
            counterLineLeft.classList.add('active')
            counterCircle.classList.add('active-left')

            counterLineRight.classList.remove('active')
            counter.classList.remove('active-right')
            counter.classList.remove('active-right')
            counterCircle.classList.remove('active-right')
        }

        function switchActiveRight() {
            counterLineRight.classList.add('active')
            counter.classList.add('active-right')
            counter.classList.add('active-right')
            counterCircle.classList.add('active-right')

            counter.classList.remove('active-left')
            counter.classList.remove('active-left')
            counterCircle.classList.remove('active-left')
            counterLineLeft.classList.remove('active')
            console.log("mobRight")
        }

        setInterval(switchActiveLeft, 3000)
        setInterval(switchActiveRight, 6000)


    } else {
        let whalf = window.innerWidth / 2,
            where = document.querySelector('body')

        document.onmousemove = function(event) {
            if (event.pageX < whalf) {
                console.log('left')
                counter.classList.add('active-left')
                counter.classList.add('active-left')
                counterLineLeft.classList.add('active')
                counterCircle.classList.add('active-left')

                counterLineRight.classList.remove('active')
                counter.classList.remove('active-right')
                counter.classList.remove('active-right')
                counterCircle.classList.remove('active-right')


            } else {
                console.log('right')
                counterLineRight.classList.add('active')
                counter.classList.add('active-right')
                counter.classList.add('active-right')
                counterCircle.classList.add('active-right')

                counter.classList.remove('active-left')
                counter.classList.remove('active-left')
                counterCircle.classList.remove('active-left')
                counterLineLeft.classList.remove('active')
            }
        }
    }
    window.addEventListener('scroll' , function(){
        const BgImg = document.querySelector('.two-page');
        let WinPos  = window.pageYOffset;
        BgImg.style.marginTop = -WinPos * .20 + "px"
    });



})
