const navigationInit = () => {
    const links = document.querySelectorAll('.navigation_links') // ищем все навигационные ссылки
    const sections = document.querySelectorAll('.navigation-section') // ищем все секции
    const hamburgerLinks = document.querySelectorAll(".hamburger-menu .navigation_links")
    const sideBarLinks = document.querySelectorAll(".sidebars-menu .navigation_links")
    var swiper = new Swiper(".hamburger-menu-slider", {
        direction: "vertical",
        loop: true,
        // watchSlidesProgress: true,
        slidesPerView: 5,
        centeredSlides: true,
        mousewheel: true,
        allowTouchMove: false,
        navigation: {
            nextEl: ".hamburger-menu-slider-button-next",
            prevEl: ".hamburger-menu-slider-button-prev",
        },
    });
    sections.forEach(section => { // для каждой секции
        const sectionOffset = section.getBoundingClientRect();
        links.forEach(link => { // для каждой ссылки
            link.classList.remove('navigation_links_active') // удаляем активный класс
        })

        if (window.pageYOffset >= sectionOffset.top) { // проверяем, если страница прокручена больше, чем расстояние секции от начала страницы
            links.forEach(link => { // для каждой ссылки
                link.classList.remove('navigation_links_active') // удаляем активный класс
                if (link.dataset.section === section.dataset.section) { // проверяем, если data-атрибуты ссылки и секции совпадают
                    link.classList.add('navigation_links_active') // добавляем ссылке активный класс
                }
            })
        }
    })

    // set default active
    // if ([...hamburgerLinks].every(link => !link.classList.contains("navigation_links_active"))) {
    //     hamburgerLinks.forEach((item, index) => {
    //         if (!index) {
    //             item.classList.add('navigation_links_active');
    //         }
    //     })
    // }
    // if ([...sideBarLinks].every(link => !link.classList.contains("navigation_links_active"))) {
    //     sideBarLinks.forEach((item, index) => {
    //         if (!index) {
    //             item.classList.add('navigation_links_active');
    //         }
    //     })
    // }
}
