const playAudio = () => {
    const pauseBtn = document.querySelectorAll(".music-player_btn-item-pause")
    const playBtn = document.querySelectorAll(".music-player_btn-item-play")
    const btnWrapper = document.querySelectorAll(".music-player")
    const player = document.querySelector("#player")
    const playerListener = () => {
        if (player.paused) {
            player.play().then(() => {
                playBtn.forEach((item) => item.classList.remove('active'))
                pauseBtn.forEach((item) => item.classList.add('active'))
            });
        } else {
            playBtn.forEach((item) => item.classList.add('active'))
            pauseBtn.forEach((item) => item.classList.remove('active'))
            player.pause();
        }
    }
    btnWrapper.forEach(item => item.addEventListener('click', playerListener))

    return playerListener
}
