const body = document.body;
const triggerMenu = document.querySelector(".trigger_menu");
const leftLogo = document.querySelector(".header_left__logo");

const scrollUp = "scroll-up";
const scrollDown = "scroll-down";
let lastScroll = 0;
triggerMenu.addEventListener("click", () => {
    body.classList.toggle("menu-open");
});

const visibleHeaderLogo = () => {
    const section = document.querySelector('.two-page')
    const sectionOffset = section.getBoundingClientRect();

    if (sectionOffset.top <= 0) {
        leftLogo.classList.add("visible")
    } else {
        leftLogo.classList.remove("visible")
    }
}

const headerScroll = () => {
    const currentScroll = window.pageYOffset;
    if (currentScroll === 0) {
        body.classList.remove(scrollUp);
        return;
    }
    if (body.classList.contains("menu-open")) {
        body.classList.remove(scrollUp);
        return;
    }

    if (currentScroll > lastScroll && !body.classList.contains(scrollDown)) {
        // down
        body.classList.remove(scrollUp);
        body.classList.add(scrollDown);
    } else if (
        currentScroll < lastScroll &&
        body.classList.contains(scrollDown)
    ) {
        // up
        body.classList.remove(scrollDown);
        body.classList.add(scrollUp);
    }
    lastScroll = currentScroll;
}
